<template>
  <div class="container">
    <div class="nav">
      <div>
        <h1>Idea Hub</h1>
        <h4>Got an awesome idea? Let others know and contribute their opinion.</h4>
      </div>
    </div>
    <h3>Latest Posts</h3>
    <hr>
    <div class="create-post">
      <form @submit.prevent="handleCreatePost(text)">
        <label>Say something:</label>
        <input type="text" placeholder="Create a post" v-model="text">
        <button>post</button>
      </form>
      <div v-if="error" class="error">
        {{error}}
      </div>
    </div>
    <div v-if="posts.length">
      <div class="post" v-for="post in posts" :key="post.text">
        <div class="single-post">
          <div>
            <h3>{{post.text}}</h3>
          </div>
          <div class="action">
            <span class="material-icons" @click="handleDeletePost(post._id) ">delete</span>
          </div>
        </div>
      </div>
    </div>
    <div v-else>Loading....</div>
  </div>
</template>

<script>
import {computed, ref} from 'vue';
import {useStore} from 'vuex';
import {useRouter} from 'vue-router'
export default {
  name: 'PostComponent',
  
  setup(){

    let posts = ref([]);
    const store = useStore();
    const router = useRouter();
    store.dispatch('loadPosts');
    posts = ref(computed(()=>store.state.posts));
    let text = ref('');
    let error = ref('');

    const handleCreatePost = async (post) =>{
      text.value = '';
      if(post !== ''){
        await store.dispatch('createPost',{text:post});
      router.go({name: 'Home'});
      }else{
        error.value = 'Write something to post!!!'
      }
    }
     
    const handleDeletePost = async (id) => {
      alert(`Delete this project?`)
      await store.dispatch('deletePost',{id});
      router.go({name: 'Home'});
      // await store.dispatch('loadPosts');
      // posts = ref(computed(()=>store.state.posts));
    }
    
    return{
      posts,
      text,
      error,
      handleCreatePost,
      handleDeletePost
    }
  }
}
</script>

<style scoped>
  .container{
    max-width: 1200px;
    margin: 0 auto;
  }
  .post {
    background: rgb(182, 252, 168);
    padding: 30px 0;
    margin: 10px auto;
  }
  .single-post {
    display: grid;
    grid-template-columns: 10fr 2fr;
    justify-content: center;
    align-items: center;
  }
  .material-icons {
    cursor: pointer;
  }
</style>
